  font-size: 28px;
  font-family: "Ubu-B";
  line-height: 150%;
}
@media (max-width: 768px) {
  .our-service__box p {
    font-size: 24px;
    flex: 0 1 100%;
  }
}
.our-service__box a {
  display: flex;
  font-family: "Ubu-B";
  font-size: 16px;
  height: 52px;
  color: #fff;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  text-align: center;
  align-items: center;
  background-color: #ff5a30;
  border-radius: 4px;
  border: 1px solid transparent;
  padding: 0 40px;
  transition: all 0.4s ease;
}
.our-service__box a:hover {
  background: #fff;
  color: #ff5a30;
  border: 1px solid #ff5a30;
}
@media (max-width: 768px) {
  .our-service__box a {
    height: 30px;
  }
}

.our-service__item-box:hover {
  background: rgba(30, 33, 44, 0.6);
}
.our-service__item-box:hover .our-service__item-box-title,
.our-service__item-box:hover .our-service__item-box-svg {
  color: #fff;
  fill: #fff;
}

.our-service__item:hover img {
  top: -10px;
  right: -10px;
  bottom: -10px;
  left: -10px;
  width: calc(100% + 10px);
  height: calc(100% + 10px);
}

body {
  width: 100%;
  min-height: 100%;
  font-family: "Ubu-R";
  -webkit-user-select: none;
          user-select: none;
}

.wrapper {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template: minmax(90px, auto) 1fr minmax(1fr, 825px) minmax(566px, auto)/1fr;
  grid-template-areas: "header" "main" "discuss" "footer";
}

.header {
  grid-area: header;
  display: grid;
  grid-template: 1fr/1fr minmax(auto, 1230px) 1fr;
  padding: 0 15px;
  z-index: 1000;
  position: sticky;
  top: 0;
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
  background-color: rgba(255, 255, 255, 0.4);
}

.main {
  grid-area: main;
  display: flex;
  flex-direction: column;
}
.main section {
  display: grid;
  grid-template: 1fr/1fr minmax(auto, 1230px) 1fr;
  padding: 0 15px;
}
.main section .center {
  grid-column: 2/3;
}

.discuss {
  grid-area: discuss;
  display: grid;
  grid-template: 1fr/1fr minmax(auto, 1230px) 1fr;
  padding: 0 15px;
}

.footer {
  grid-area: footer;
  display: grid;
  grid-template: 1fr/1fr minmax(auto, 1230px) 1fr;
  padding: 0 15px;
  background-color: #1e212c;
}

.parallax-block {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 90px;
  z-index: -1;
}
.parallax-block img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  -o-object-fit: cover;
     object-fit: cover;
}

.go-top {
  position: fixed;
  display: flex;
  column-gap: 16px;
  bottom: 4%;
  right: 4%;
  background: none;
  opacity: 0;
  transition: all 0.5s ease;
  font-weight: 600;
  text-shadow: 0 0 4px rgba(255, 255, 255, 0.6);
  z-index: 100;
}
.go-top__text {
  align-self: flex-end;
  font-size: 14px;
  color: #ff5a30;
  line-height: 150%;
}
@media (max-width: 992px) {
  .go-top__text {
    margin: 10px 0 0;
    align-self: flex-end;
    writing-mode: vertical-lr;
    font-size: 16px;
  }
}
.go-top__squer {
  width: 40px;
  height: 40px;
  border-radius: 4px;
  background: #ff5a30;
  position: relative;
  overflow: hidden;
}
.go-top__squer::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  top: 65%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  border-left: 3px solid #fff;
  border-top: 3px solid #fff;
}
@media (max-width: 992px) {
  .go-top__squer {
    width: 15px;
    height: 25px;
  }
  .go-top__squer::before {
    width: 10px;
    height: 10px;
  }
}
@media (max-width: 992px) {
  .go-top {
    flex-direction: column-reverse;
    right: 5px;
    align-items: center;
  }
}

.success-block {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  top: 0;
  left: -1px;
  text-align: center;
  background-color: #1e212c;
  border: 1px solid #ff5a30;
  border-radius: 6px;
  box-shadow: 0 0 10px 1px rgba(255, 90, 48, 0.6);
  z-index: 100;
  transition: all 0.5s ease;
  transform: scaleY(0);
}
.success-block h5 {
  margin: 40px 0 0;
  font-size: 40px;
  -webkit-user-select: none;
          user-select: none;
  font-family: "Ubu-B";
  color: #ff5a30;
  text-shadow: 0 0 4px rgba(255, 255, 255, 0.5);
  letter-spacing: 0.2rem;
}
.success-block p {
  margin: 50px 30px 0;
  font-size: 30px;
  line-height: 130%;
}
.success-block p::before {
  content: attr(data-success-text);
  color: #fff;
}
.success-block button {
  margin: 100px 0 40px;
  align-self: center;
  display: flex;
  font-family: "Ubu-B";
  font-size: 16px;
  height: 52px;
  color: #fff;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  text-align: center;
  align-items: center;
  background-color: #ff5a30;
  border-radius: 4px;
  border: 1px solid transparent;
  padding: 0 40px;
  transition: all 0.4s ease;
}
.success-block button:hover {
  background: #fff;
  color: #ff5a30;
  border: 1px solid #ff5a30;
